*{
    margin:0;
    padding:0;
    box-sizing: border-box;
}
.navi {
    min-height: 50px;
    width: 100%;
    height: auto;
    background-color: gray;
    padding: 0px;
    justify-content: center;  /*在flex中垂直居中*/
    display: flex;
    align-items: center;
    font-size: 25px;
    -webkit-margin-after: 0.83em;
            margin-block-end: 0.83em;
    font-weight: bold;
    
}
.container{
    padding: 20px 0px 0px 10px;
    width: 100%;
}
.singleBox{
    display: flex;
    flex-wrap: wrap;
    height: auto;
    margin-left: auto;
    margin-right: auto;
    width: 90%
}
.label_item{
    flex:1 0 8%;
}
@media screen and (max-width:991px) {
    .singleBox label{
        flex:1 0 15%;
    }
}
.wrap_item{
    border-radius: 5px;
    width:5%;
    height: 20px;
}
@media screen and (max-width:600px) {
    .singleBox label{
        flex:1 0 25%
    }
}
